import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'gd-header',
  templateUrl: './gd-header.component.html',
  styleUrls: ['./gd-header.component.scss'],
})
export class GdHeaderComponent implements OnInit {

  @Input() breadcrumbList;
  @Input() avatarSrc;
  @Input() avatarSize;
  @Input() employeeName;
  @Input() searchBar;
  @Input() logoSrc;

  @Output() arrowClick : EventEmitter<any> = new EventEmitter();

  arrowIcon:boolean=true;

  constructor() { }

  ngOnInit() {
    console.log(this.logoSrc);
  }


  doArrowClick(){
    this.arrowClick.emit();
    this.arrowIcon=!this.arrowIcon;
  }

}
