import { Component, OnInit, Input } from '@angular/core';
import { ThemePalette } from '@angular/material';

@Component({
  selector: 'gd-progressbar',
  templateUrl: './gd-progressbar.component.html',
  styleUrls: ['./gd-progressbar.component.scss'],
})
export class GdProgressbarComponent implements OnInit {
  @Input()bufferValue: number;
  @Input()color: ThemePalette;
  @Input()mode: 'determinate' | 'indeterminate' | 'buffer' | 'query';
  @Input()value: number;
  constructor() { }

  ngOnInit() {}

}
