import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { FormBuilder } from '@angular/forms';
import { Validators } from '@angular/forms';
import { Input } from '@angular/core';

@Component({
  selector: 'gd-stepper',
  templateUrl: './gd-stepper.component.html',
  styleUrls: ['./gd-stepper.component.scss'],
})
export class GdStepperComponent implements OnInit {
  
  arraySection:any;
  i:any = 0;

  constructor() {
    
   }

  ngOnInit() {
    this.arraySection = document.querySelectorAll(".gd-stepper-section");
  }

  ngAfterViewInit(){
    this.arraySection.forEach(element => {
      var selector = ".gd-stepper-section-" + this.i;
      document.querySelector(selector).innerHTML = element.outerHTML;
      this.i++;
    });
  }

}
