import { Component, OnInit, Input } from '@angular/core';
import { ThemePalette } from '@angular/material';
import 'hammerjs';
@Component({
  selector: 'gd-slider',
  templateUrl: './gd-slider.component.html',
  styleUrls: ['./gd-slider.component.scss'],
})
export class GdSliderComponent implements OnInit {
  @Input()color: ThemePalette;
  @Input()disabled: boolean;
  @Input()max: number;
  @Input()min: number;
  @Input()invert: boolean;
  @Input()value: number | null;
  @Input()vertical: boolean;
  @Input()step: number;
  @Input()thumbLabel: boolean;
  @Input()tickInterval: number | "auto";
  constructor() { }

  ngOnInit() {
    console.log(this.max ,"1asdasd")
  }

}
