import { Component, OnInit, Input } from '@angular/core';
import { ThemePalette } from '@angular/material';

export interface Section {
  name: string;
  updated: Date;
}   
@Component({
  selector: 'gd-list',
  templateUrl: './gd-list.component.html',
  styleUrls: ['./gd-list.component.scss'],
})

export class GdListComponent implements OnInit {
  @Input() text:any;
  @Input()color: ThemePalette;
  @Input()disableRipple: boolean;
  @Input()checkboxPosition: 'before' | 'after';
  @Input()selected: boolean;
  @Input()value: any;
  @Input()disabled: boolean;
  constructor() { }

  folders: Section[] = [
    {
      name: 'Photos',
      updated: new Date('2/13/19'),
    },
    {
      name: 'Recipes',
      updated: new Date('5/15/19'),
    },
    {
      name: 'Work',
      updated: new Date('7/28/19'),
    },
    {
      name: 'University',
      updated: new Date('9/20/19'),
    }
  ];

  ngOnInit() {}

}
